<template>
  <div id="avatar-wrapper"  :style="{ height: height + 'px', width : width + 'px'}">

      <div id="divVHSS"></div>

  </div>
</template>

<script>
export default {
  name: "avatar",
  props: {
    initialWidth: {
      type: Number,
      required: true
    }
    },

  data() {
    return{
      width: this.initialWidth,
    }
  },
  computed: {
    height: {
      get: function () {
        return this.width * 1.125;
      },
      set: function (newVal) {
        this.width = newVal * 8/9;
      }
    }
  },
  methods:{
    avatarResize(){
      console.log('New Width is ', this.width)
      console.log('New Height is', this.height)
      window.dynamicResize(this.width, this.height)
    }
  },
  watch: {
    width: function () {
      window.dynamicResize(this.width * 1.5, this.height);
      console.log('canvas canvas canvas')
    },
  },
  mounted() {
    //const that = this
    // window.vh_sceneLoaded = function(){
    //   console.log('It came in here')
    //   that.avatarResize()
    // }
  }
}
</script>

<style scoped>
#avatar-accessories {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: flex-end;
  flex-direction: column;
}



