import VueX from 'vuex';
import Vue from 'vue';

Vue.use(VueX);

export const store = new VueX.Store(
    {
        state: {
            isListening: false,
            isOn: false,
            stopListening: '',
            isSpeaking: false,
            isTouched: false,
            userSpeechInput: ''
        },
        getters: {
            isListeningState: state => state.isListening,
            isOnState: state => state.isOn,
            isStopListening: state => state.stopListening,
            isSpeakingState: state => state.isSpeaking,
            isTouchedState: state => state.isTouched,
            userSpeechInputUpdated: state => state.userSpeechInput
        },
        mutations: {
            updateUserSpeechInput: (state, userSpeechInput ) => {
                state.userSpeechInput = userSpeechInput
            },
            updateListeningFlag: (state, isListening) =>{
                state.isListening = isListening
            },
            updateOnFlag: (state, isOn) => {
                state.isOn = isOn
            },
            updateStopListening: (state, stopListening) => {
                state.stopListening = stopListening
            },
            updateIsSpeaking: (state, isSpeaking) => {
                state.isSpeaking = isSpeaking
            },
            updateIsTouched: (state, isTouched) => {
                state.isTouched = isTouched
            }
        },
        actions: {
            updateListeningFlag: (context, isListening) => {
                context.commit('isListening', isListening)
            },
            updateOnFlag: (context, isOn) => {
                context.commit('isOn', isOn)
            }
        }
    })

