<template>

  <v-dialog v-model="dialog" width="500" height="300">
    <v-card>
      <v-card-title class="black--text" align="center"> Please Select an Option </v-card-title>
      <v-card-text align="center">
        <v-btn v-for="(optionList,i) in optionLists" :key="i" width="400" class="mt-5" @click="currentSelection(optionList)">{{ optionList }} </v-btn>
      </v-card-text>
    </v-card>
  </v-dialog>

</template>

<script>
import {EventBus} from "@/event-bus";

export default {
  name: "optionsDialog",
  data() {
    return {
      dialog: false,
      optionLists: ["Missed Court Date", "Can't make scheduled Court Date", "Driving Privileges", "Forfeited License"]
    }
  },
mounted() {
  EventBus.$on('openOptionsDialog', () => {
    this.dialog =!this.dialog
  })
},
  methods: {
    currentSelection(result){
      console.log('What is Selected', result)
    }
  }
}
</script>

