<template>
  <v-responsive :aspect-ratio="16/9">

<!--    ----------------------------------------------------- ROW 1 ------------------------------------------------  -->

    <v-row>
      <v-col cols="1" class="mt-2">
        <v-icon size="60" @click="$router.push('/')">mdi-arrow-left-circle</v-icon>
      </v-col>
      <v-col cols="11" align="center" class="mt-4">
        <p class="text-h4">Frequently Asked Questions</p>
        <v-divider align="center" justify="center" width="600" height="30"></v-divider>
      </v-col>
    </v-row>

<!--    ---------------------------------------------------- ROW 2 --------------------------------------------------  -->

    <v-row>
      <v-col cols="12" align="center">
        <v-text-field v-model="search" label="Type a Keyword or a Phrase to search"
                      outlined prepend-inner-icon="mdi-magnify">
        </v-text-field>
      </v-col>
    </v-row>

<!--    ---------------------------------------------------- ROW 3 ---------------------------------------------------- -->

    <v-row>
      <v-col cols="12" align="center">
        <v-card align="center" width="1800" height="500">
          <v-expansion-panels multiple>
            <v-expansion-panel v-for="(faq,i) in faqs" :key="i" class="mt-1">
              <v-expansion-panel-header color="cyan">{{ faq.question }}</v-expansion-panel-header>
              <v-expansion-panel-content>{{ faq.answer }}</v-expansion-panel-content>
            </v-expansion-panel>
          </v-expansion-panels>
        </v-card>
      </v-col>
    </v-row>
<!--    ---------------------------------------------------- ROW 4 ---------------------------------------------------- -->
    <v-row>
      <v-col cols="3">
<!--        <avatar :initial-width="250"></avatar>-->
      </v-col>
      <v-col cols="9" align="center">
        <v-card outlined width="1300" min-height="250" id="card-wrapper" class="mt-5">
          <p class="ma-5">{{ displayText }}</p>
        </v-card>
      </v-col>
    </v-row>

  </v-responsive>
</template>

<script>

import db from '@/firebase'
//import avatar from "@/components/avatar";

export default {
  name: "Faqs",
  components:{
  //  avatar
  },
  data() {
    return {
      search: '',
      faqs: [],
      displayText: 'Scroll through the list to select the question or search for a FAQ by keyword'
    }
  },
  created() {
    db.collection('faqs').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const sample = doc.data().answer

        const data = {
          'id': doc.id,
          'question': doc.data().question,
          'answer': sample.replaceAll("\\n", "\n")
        }
        this.faqs.push(data)
      })
    })
  },
}
</script>

<style scoped>
/deep/ .v-text-field {
  width: 1800px;
}

/deep/ .v-card {
  overflow-y: auto;
  border-style: double;
}

/deep/ .v-expansion-panels {
  width: 95%;
}

#card-wrapper{
  background-color: rgba(30, 101, 39, 0.88);
  border-radius: 35px;
  color: white;
  text-align: center;
  border-style: solid;
  border-color: white;
  border-width: 2px;
  z-index: 999999;
  padding: 0 1em;
}

.v-card p{
  font-size: 3em;
}
</style>
