import {EventBus} from "@/event-bus";
import {store} from "@/store"

let host = 'https://ottawabot.azurewebsites.net/qnamaker';
let endpoint_key = 'e3baa3b7-7583-4ff8-b8ac-05c3e82662e4';
let route = '/knowledgebases/e9186341-abca-4b15-ac0a-978e1e9f40c7/generateAnswer';
let request_as_promised = require('request-promise');
let question, mapIs;

import router from "@/router";

let sleep = (milliseconds) => {
    const date = Date.now();
    let currentDate = null;
    do {
        currentDate = Date.now();
    } while (currentDate - date < milliseconds);
}

let emitGlobalImageOpenEvent = (map) => {
    console.log('Map me kya value aayi = ', map)
    EventBus.$emit('openImageDialog', map);
}


let emitGlobalTextOpenEvent = (text) => {
    EventBus.$emit('openTextDialog', text);
}

// let emitGlobalTextCLoseEvent = () => {
//     EventBus.$emit('closeTextDialog');
// }


let connection = async (transcript) => {
    question = {
        'question': transcript,
        'top': 3,
        'scoreThreshold': 20,
        'rankerType': 'QuestionOnly'
    };
    console.log('Host is', host)
    let options = {
        uri: host + route,
        method: 'POST',
        headers: {
            'Authorization': "EndpointKey " + endpoint_key
        },
        json: true,
        body: question
    };
    let response = await request_as_promised.post(options);
    console.log('Connection happened')
    return response;
}

let getAnswer = function (transcript) {
    if (transcript !== '') {
        let final = ''
        connection(transcript).then(function (a) {
            a = JSON.parse(JSON.stringify(a.answers));
            //console.log('Answer from QnA', a.answers[0] )
            try {
                final = a[0].metadata.find(({name}) => name === 'intent').value
                store.commit('updateListeningFlag', false)
                console.log('What is there in final at error', final)
            } catch (e) {
                let name = 'I am still learning. Please go to Sheriffs Office for Further Assistance';
                let path = "assets/maps/up/o-sheriff.png"
                let imageDescription = 'Sheriffs Office'
                let map = {imageDescription, path, name}
                sleep(300)
                emitGlobalImageOpenEvent(map)
                //store.commit('updateIsSpeaking', true)
                store.dispatch('playSpeechInFuro', name)
            }

            if (final === 'navigate') {
                console.log(final)
                console.log('Answer from the QnA is', a[0].answer);
                let text = a[0].answer
                //store.commit('updateIsSpeaking', true)
                store.dispatch('playSpeechInFuro', text)
                router.push({path: '/' + a[0].metadata.find(({name}) => name === 'navigateto').value}).then(r => console.log(r))
            } else if (final === 'showmap') {
                try {
                    sleep(300)
                    mapIs = a[0].metadata.find(({name}) => name === 'mappath').value
                    let position = a[0].metadata.find(({name}) => name === 'mapfloor').value

                    if (position === "upper") {
                        let name = a[0].answer;
                        let path = "assets/maps/up/" + mapIs
                        let imageDescription = a[0].questions[0]
                        let map = {imageDescription, path, name}
                        sleep(300)
                        emitGlobalImageOpenEvent(map)
                        //store.commit('updateIsSpeaking', true)
                        store.dispatch('playSpeechInFuro', name)
                    } else if (position === "lowerlevel") {
                        let name = a[0].answer;
                        let path = "assets/maps/lowerlevel/" + mapIs
                        let imageDescription = a[0].questions[0]
                        let map = {imageDescription, path, name}
                        sleep(500)
                        emitGlobalImageOpenEvent(map)
                        //store.commit('updateIsSpeaking', true)
                        store.dispatch('playSpeechInFuro', name)
                    }
                } catch (e) {
                    console.log(e.message())
                }
            } else if (final === 'showtext') {
                if (a[0].answer !== '') {
                    console.log(a[0])
                    let question = a[0].questions[0]
                    let answer = a[0].answer
                    let text = {question, answer}
                    sleep(500)
                    emitGlobalTextOpenEvent(text)
                    //store.commit('updateIsSpeaking', true)
                    store.dispatch('playSpeechInFuro', answer)
                }
            }
        })
    }else{
        store.commit('updateIsSpeaking', false)
        console.log('Speaking is set to false')
    }


}

export {getAnswer}
