import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home'
import Faqs from "@/views/Faqs";
import Eviction from "@/views/Eviction";
import Maps from "@/views/Maps";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/Faqs',
        name: 'Faqs',
        component: Faqs
    },
    {
        path: '/Eviction',
        name: 'Eviction',
        component: Eviction
    },
    {
        path: '/Maps',
        name: 'Maps',
        component: Maps
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router
