<template>
  <v-dialog max-height="800px" max-width="800px" persistent>
    <v-card height="300px">
      <v-card-title min-height='75px'
                    class="headline orange darken-2 pt-3 h-3 pb-3 font-weight-bold justify-center white--text">
        {{ currentState }}
        <v-icon color="red darken-4" class="pl-5 Blink">
          mdi-microphone
        </v-icon>
      </v-card-title>
      <v-card-text>
        <v-card-title class="font-weight-bold text-h4 pa-10 justify-center align-center"> {{ finalTranscript }}
        </v-card-title>
      </v-card-text>
    </v-card>
  </v-dialog>
</template>

<script>

import {getAnswer} from "@/components/azureQnA";
const blazeFace = require('@tensorflow-models/blazeface');
import * as tf from '@tensorflow/tfjs-core';
import '@tensorflow/tfjs-backend-webgl';
import {EventBus} from "@/event-bus";


export default {
  name: "speechRecognition",
  data() {
    return {
      dialog: false,
      transcript: '',
      currentState: '',
      finalTranscript: '',
      timer: 10000,
      count: 0,
      greet: 0,
      faceDistance: null,
      distanceFaceDynamic: 18,
      timeout1: null,
      counter: 0
    }
  },
  computed: {
    //Getter
    isListeningState() {
      return this.$store.getters.isListeningState;
    },

    isOnState() {
      return this.$store.getters.isOnState;
    },

    isSpeakingState() {
      return this.$store.getters.isSpeakingState;
    },

    isTouchedState() {
      return this.$store.getters.isTouchedState;
    },

    userSpeechInputUpdated() {
      return this.$store.getters.userSpeechInputUpdated;
    }

  },
  methods: {

    emitGlobalImageCLoseEvent() {
      EventBus.$emit('closeImageDialog');
    },
    emitGlobalTextCLoseEvent() {
      EventBus.$emit('closeTextDialog');
    },
    emitGlobalTextOpenEvent(text) {
      EventBus.$emit('openTextDialog', text);
    },

    startRecognition() {
      const that = this
      const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition
      if (!SpeechRecognition) {
        // uh shit browser doesn't support speech, do things here
      }

      const recognition = new SpeechRecognition()
      recognition.continuous = true;
      recognition.interimResults = true;
      recognition.lang = 'en-US';

      // When a person comes for the first time(If section will be executed only once). Session Start
      // that.timer is the timeout for setInterval, this is added to resolve Reference issue caused due to setTimeout inside setInterval
      recognition.start()



      // Recognition start Listener
      // Listens everytime recognition is started
      recognition.onstart = () => {
        console.log('Recognition Started')
      }


      // Recognition On Result event.
      // It is executed only when input speech is provided.
      // If recognition is started and logic does not allow On Result to execute, On Error event is triggered giving Error Code = 'ABORTED'

      recognition.onresult = (event) => {
        //that.emitGlobalImageCLoseEvent()
        //that.emitGlobalTextCLoseEvent()
        let spResult = event.results[event.resultIndex];
        console.log('SP Result Index ', spResult)
        if (!that.isSpeakingState && that.isOnState) {
          that.dialog = true
          that.currentState = "I am Listening"
        }
        if (that.isOnState && that.isListeningState && that.transcript.length === 0) {

          if (!that.isSpeakingState) {
            that.transcript = event.results[event.results.length - 1][0].transcript;
            that.$store.commit('updateUserSpeechInput', that.transcript)
            that.finalTranscript = that.transcript
          }

        } else if (that.transcript.length >= 1 && that.isListeningState) {

          if (!that.isSpeakingState) {
            that.transcript = event.results[event.results.length - 1][0].transcript;
            that.finalTranscript = that.transcript
          }


        } else {
          that.transcript = ''
        }

        // after speech is complete isFinal will be true, which then triggers a call to Azure Cognitive Service (QnA Maker)
        if (spResult.isFinal) {
          if (that.isListeningState && that.transcript.split(" ").length <= 50) {
            console.log('The transcript is  ', that.transcript)
            that.sleep(300)


            // please refer azureQnA.js for execution info of getAnswer method.
            if (!that.isSpeakingState && that.transcript !== '') {
              that.dialog = false
              console.log('It should not be here')
              getAnswer(that.finalTranscript)
            }

            that.currentState = ''
            that.transcript = ''
            that.finalTranscript = ''


            //recognition.stop()
            that.$store.commit('updateIsSpeaking', true)
            that.$store.commit('updateListeningFlag', false)
          }
        }
      }

      // On End event is triggered when recognition.stop() is called
      recognition.onend = () => {
        this.$store.commit('updateListeningFlag', false)
        // if (!that.isSpeakingState) {
        //   setTimeout(function (){recognition.start()},100)
        // }
        if (!that.isTouchedState) {
          setTimeout(function () {
            recognition.start()
          }, 100)
        }

        console.log('Recognition Stopped')
      }


      // recognition.onerror = (event) => {
      //   let text , question, answer;
      //   switch(event.error){
      //     case "no-speech":
      //       console.log("Recognition Error -  No speech recognised")
      //       text = 'Recognition Error -  No speech recognised. Can you please ask again'
      //       that.$store.dispatch('playSpeechInFuro', text)
      //       that.$store.commit('updateIsSpeaking', true)
      //       break;
      //     case "aborted":
      //       console.log("Recognition Error -  Aborted")
      //       break;
      //     case "network":
      //       console.log("Recognition Error -  Network Error")
      //       break;
      //     case "bad-grammar":
      //       console.log("Recognition Error -  Improve your English")
      //       break;
      //     case "language-not-supported":
      //       console.log("Recognition Error -  Please use English")
      //       question = 'Sorry for the inconvenience'
      //       answer = 'Recognition Error - I only understand English'
      //       text = {question, answer}
      //       that.emitGlobalTextOpenEvent(text)
      //       that.$store.dispatch('playSpeechInFuro', answer)
      //       that.$store.commit('updateIsSpeaking', true)
      //       break;
      //   }
      // }

    },

    //Accessing Camera for face detection using getUserMedia
    initiateCamera() {
      const that = this
      that.video = document.getElementById('video');

      navigator.mediaDevices.getUserMedia({
        'audio': false,
        'video': {width: 500, height: 500, frameRate: {max: 20}}
      })
          .then(
              stream => {
                that.video.srcObject = stream;
                that.video.play().then(
                    () => {
                      this.video.width = this.video.videoWidth;
                      this.video.height = this.video.videoHeight;
                      tf.setBackend('webgl').then(
                          () => {
                            console.log('Webgl is the backend')
                            blazeFace.load().then(
                                model => {
                                  console.log('Blaze Face loaded')

                                  setInterval(function () {
                                        that.prediction(model)
                                      }
                                      , 1000);
                                }
                            )
                          }
                      )
                    }
                )
              }
          )
    },

    prediction(model) {
      const that = this
      const returnTensors = false;
      const flipHorizontal = true;
      const annotateBoxes = true;
      this.faceDistance = 0;

      model.estimateFaces(this.video, returnTensors, flipHorizontal, annotateBoxes)
          .then(
              prediction => {
                if (prediction.length > 0) {
                  for (let i = 0; i < prediction.length; i++) {
                    const start = prediction[0].topLeft;
                    const end = prediction[0].bottomRight;
                    that.faceDistance = (((start[0] - end[0]) / 500) * 100).toFixed(2);
                    // setInterval(function() {
                    //   that.faceDistance = (((start[0] - end[0]) / 500) * 100).toFixed(2);
                    //   },200)
                    console.log('Dynamic Face Distance after initially Detected', that.distanceFaceDynamic)
                    if (that.faceDistance >= that.distanceFaceDynamic && prediction[0].probability[0] >= 0.95) {

                      if (that.distanceFaceDynamic === 18) {
                        that.distanceFaceDynamic = that.faceDistance - 5
                        console.log('Initial Face Distance Detected', that.faceDistance)
                        console.log('Dynamic Face Distance Calculated after Detected', that.distanceFaceDynamic)

                      }
                      let rEyeEarDiff = prediction[0].landmarks[0][0] - prediction[0].landmarks[4][0];
                      let lEyeEarDiff = prediction[0].landmarks[1][0] - prediction[0].landmarks[5][0];

                      if (rEyeEarDiff < -15 && lEyeEarDiff > 15) {
                        console.log('Probability of the Prediction', prediction[0].probability[0])
                        console.log('Face Detection condition passed')
                        console.log('Person to Robot Camera Distance', that.faceDistance)
                        this.$store.commit('updateOnFlag', true)
                        that.count = 0
                        if (that.isOnState) {
                          that.timer = 10000
                          clearTimeout(that.timeout1)
                          console.log('Timeout value', that.timeout1)
                          if (!that.isSpeakingState) {
                            // if (that.greet === 0) {
                            //   that.dialog = true
                            //   that.currentState = 'Hello, I am Francesca'
                            //   that.finalTranscript = 'How can I help you?'
                            //   that.greet = 1
                            //   that.distanceFaceDynamic = 18
                            //   that.$store.commit('updateIsSpeaking', true)
                            //   let text = that.currentState + that.finalTranscript
                            //   that.$store.dispatch('playSpeechInFuro', text)
                            //
                            // }
                            if (!that.isSpeakingState) {
                              that.currentState = 'I am Listening'
                              that.finalTranscript = ''
                            }

                            this.$store.commit('updateListeningFlag', true)
                          }
                        } else {
                          //that.timer = 0
                          that.timer = 10000
                        }

                      } else {
                        console.log('Person not facing towards Camera')
                        that.$store.commit('updateOnFlag', false)
                        //that.$store.commit('updateListeningFlag', false)
                      }
                    } else {
                      console.log('Person not in scope of Device')
                      that.$store.commit('updateOnFlag', false)
                      //that.$store.commit('updateListeningFlag', false)

                      // below is written to execute greeting method only once
                      that.count = that.count + 1
                      if (that.count === 1) {
                        that.greeting()
                      }
                    }
                  }
                } else {
                  console.log('Person not in front of Camera')
                  that.$store.commit('updateOnFlag', false)
                  //that.$store.commit('updateListeningFlag', false)

                  // below is written to execute greeting method only once
                  that.count = that.count + 1
                  if (that.count === 1) {
                    that.greeting()
                  }
                }
              })
    },
    greeting() {
      const that = this

      if (!that.isOnState) {

        that.timeout1 = window.setTimeout(function () {
          if (that.greet === 1) {
            if (window.location.pathname !== '/') {
              that.$router.push({path: '/'})
            }
            console.log('Person Left')
            that.count = 0
            that.greet = 0
            that.timer = 0
            that.distanceFaceDynamic = 18
            that.finalTranscript = ''
            that.$store.commit('updateListeningFlag', false)
            that.$store.commit('updateOnFlag', false)
            that.$store.commit('updateIsSpeaking', false)
            that.$store.commit('updateIsTouched', false)
            that.dialog = false
            that.$store.dispatch('stopSpeechInFuro', "007")
            that.emitGlobalImageCLoseEvent()
            that.emitGlobalTextCLoseEvent()
            that.startRecognition()
          }
        }, that.timer)

      }
    },
    sleep(milliseconds) {
      const date = Date.now();
      let currentDate = null;
      do {
        currentDate = Date.now();
      } while (currentDate - date < milliseconds);
    }
  },
  mounted() {
    this.initiateCamera();
    this.startRecognition();
  }
}
</script>

<style scoped>

</style>
